
'use client';
import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Plus, Upload, Users, ArrowRight, BookCopy, Settings, Calendar, Share2, PenLine } from 'lucide-react';
import { Header } from '@/components/layout/Header';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import AppShell from '@/components/layout/AppShell';
import type { ChurchSummary } from '@/types';
import { Loader2 } from 'lucide-react';
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from '@/components/ui/alert-dialog';
import { doc } from 'firebase/firestore';
import { useAuth } from '@/hooks/use-auth';
import { useFirebase, useMemoFirebase } from '@/firebase';
import { useDoc } from '@/firebase/hooks/use-doc';

export default function AdminPage() {
  const { churchId, isAdmin, isLoggedIn, isUserLoading } = useAuth();
  const { firestore } = useFirebase();
  const router = useRouter();
  const [showSetupWizardAlert, setShowSetupWizardAlert] = useState(false);

  const summaryDocRef = useMemoFirebase(() => 
    firestore && churchId ? doc(firestore, 'churches', churchId, 'summaries', 'counts') : null,
    [firestore, churchId]
  );
  
  const { data: summary, isLoading: isSummaryLoading } = useDoc<ChurchSummary>(summaryDocRef);

  useEffect(() => {
    if (isUserLoading) return;
    if (!isLoggedIn) {
      router.push('/');
    } else if (!isAdmin) {
       router.push('/my-schedule');
    }
  }, [isLoggedIn, isAdmin, router, isUserLoading]);

  useEffect(() => {
    if (!isSummaryLoading && summary !== undefined) { 
      const isNewSetup = summary === null || (summary.events === 0 && summary.templates === 0 && summary.volunteers <= 1);
      const hasShownAlert = sessionStorage.getItem('setupWizardAlertShown');

      if (isNewSetup && !hasShownAlert) {
        setShowSetupWizardAlert(true);
        sessionStorage.setItem('setupWizardAlertShown', 'true');
      }
    }
  }, [isSummaryLoading, summary]);
  
  const isLoading = isUserLoading || isSummaryLoading;

  if (isLoading) {
    return (
        <div className="flex min-h-screen w-full flex-col items-center justify-center bg-background">
            <Loader2 className="h-8 w-8 animate-spin text-primary" />
            <p className="text-muted-foreground mt-2">Loading Console...</p>
        </div>
    )
  }

  const eventCount = summary?.events ?? 0;
  const volunteerCount = summary?.volunteers ?? 0;
  const templateCount = summary?.templates ?? 0;


  return (
    <AppShell>
      <div className="flex min-h-screen w-full flex-col bg-muted/40">
        <Header />
        <main className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
          <div className="flex items-center justify-between">
            <h1 className="text-3xl font-bold font-headline tracking-tight">
              Admin Console
            </h1>
          </div>
          
          <div className="grid gap-6 md:gap-8 lg:grid-cols-2 xl:grid-cols-3">
              <Card className="lg:col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Service Management</CardTitle>
                      <CardDescription>
                          Manage, create, and import church services and events.
                      </CardDescription>
                  </CardHeader>
                  <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <Calendar className="h-5 w-5" />
                          <span>{eventCount} Upcoming Services</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/services">
                              Manage Services <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>

              <Card className="lg:col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Volunteer Management</CardTitle>
                      <CardDescription>
                          View volunteer profiles, manage assignments, and invite new members.
                      </CardDescription>
                  </CardHeader>
                  <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <Users className="h-5 w-5" />
                          <span>{volunteerCount} Volunteers</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/volunteers">
                              Manage Volunteers <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>

              <Card className="lg-col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Role &amp; Template Management</CardTitle>
                      <CardDescription>
                        Define universal roles and create service templates for quick setup.
                      </CardDescription>
                  </CardHeader>
                  <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <BookCopy className="h-5 w-5" />
                          <span>{templateCount} Templates</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/templates">
                              Manage Templates <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>

               <Card className="lg:col-span-1 xl:col-span-2">
                  <CardHeader>
                      <CardTitle>Share &amp; Export</CardTitle>
                      <CardDescription>
                        Generate bulletin content and get your public website widget.
                      </CardDescription>
                  </CardHeader>
                   <CardContent>
                      <div className="flex items-center gap-2 text-sm text-muted-foreground">
                          <Share2 className="h-5 w-5" />
                          <span>Formatted exports for newsletters and websites.</span>
                      </div>
                  </CardContent>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/share">
                              Share Schedule <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>
              
              <Card className="lg:col-span-1 xl:col-span-1">
                  <CardHeader>
                      <CardTitle>Church Settings</CardTitle>
                      <CardDescription>
                        Update your church's information and primary contact details.
                      </CardDescription>
                  </CardHeader>
                  <CardFooter>
                      <Button asChild>
                          <Link href="/admin/settings">
                              Manage Settings <ArrowRight className="ml-2 h-4 w-4" />
                          </Link>
                      </Button>
                  </CardFooter>
              </Card>
          </div>
        
        </main>
        <AlertDialog open={showSetupWizardAlert} onOpenChange={setShowSetupWizardAlert}>
            <AlertDialogContent>
                <AlertDialogHeader>
                    <AlertDialogTitle className="font-headline">Welcome to RotaScribe!</AlertDialogTitle>
                    <AlertDialogDescription>
                        It looks like you're just getting started. Would you like to run the setup wizard to help import your data and configure your church?
                    </AlertDialogDescription>
                </AlertDialogHeader>
                <AlertDialogFooter>
                    <AlertDialogCancel>Maybe Later</AlertDialogCancel>
                    <AlertDialogAction asChild>
                        <Link href="/admin/setup-wizard">
                            <PenLine className="mr-2 h-4 w-4" />
                            Run Setup Wizard
                        </Link>
                    </AlertDialogAction>
                </AlertDialogFooter>
            </AlertDialogContent>
        </AlertDialog>
      </div>
    </AppShell>
  );
}
